
html,body,#slider {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}
*{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin:0;
    padding:0;
}
ol,ul{
    list-style: none;
}
#slider{
    text-align: center;
}
.window{
    position:relative;
    width:600px;
    height:400px;
    margin:0 auto;
    overflow:hidden;
}
.container{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    position:absolute;
}
.left, .right{
    position:absolute;
    top:50%;
    -webkit-transform:translateY(-50%);
            transform:translateY(-50%);
    width:50px;
    height:50px;
    background-color:rgba(0,0,0,.3);
    border-radius:50%;
    cursor:pointer;
}
.left{
    left:3%;
    padding-left:12px;
    padding-top:10px;
}
.right{
    right:3%;
    padding-right:12px;
    padding-top:10px;
}
img{
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.dots{
    position:absolute;
    bottom:10px;
    left:50%;
    -webkit-transform:translateX(-50%);
            transform:translateX(-50%);
}
.dots li{
    display:inline-block;
    width:15px;
    height:15px;
    margin:0 3px;
    border:1px solid white;
    border-radius:50%;
    background-color:#333;
    cursor:pointer;
}
.dots .dotted{
    background-color:orange;
}
